@media screen and (max-width: 350px) {
  button {

  }
}
@media screen  {
    hr {
        height: 0.3em;
    }
    .MuiChip-label {
        word-wrap: break-word;
        overflow: hidden;
        max-width: 90vw;
    }
    .heroIconGridEmpty {
        max-width: 5em;
        min-width: 5em;
        min-height: 6.1em;
        align-items: flex-end;
        display: flex;
        font-size: 0.6em;
        font-weight: 700;
        text-align: center;
        flex-basis: 0 !important;
        flex-grow: 0;
        margin: 0;
        border-radius: 2px;
    }

    .MuiToggleButtonGroup-root {
        display: flex !important;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: center;
        max-width: 100vw;
    }
    .MuiToggleButtonGroup-root > button {
        display: flex !important;
        padding: 0.5em;
        margin: 0 !important;
        border-left-color: rgba(0, 0, 0, 0.12) !important;
      /*  border-top-right-radius: 4px !important;
        border-bottom-right-radius: 4px !important;
        border-top-left-radius: 4px !important;
        border-bottom-left-radius: 4px !important;*/
       /* border-left-style: solid !important;*/
    }
    .App {
        text-align: center;
    }

    .App-logo {
        height: 40vmin;
        pointer-events: none;
    }

    @media (prefers-reduced-motion: no-preference) {
        .App-logo {
            animation: App-logo-spin infinite 20s linear;
        }
    }

    .App-header {
        background-color: #282c34;
        min-height: 100vh;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        font-size: calc(10px + 2vmin);
        color: white;
    }

    .App-link {
        color: #61dafb;
    }

    .heroButtonThreeDots {
        font-size: 2em !important;
    }

    .heroQueueTwoVSBox {
        margin: 2px;
        display: grid;
        grid-template-rows: 1fr;
        grid-template-columns: 1fr 1fr 1fr;
    }

    .heroQueueTwoVSPosition {
        justify-content: center;
        align-items: center;
        border-radius: 200%;
        text-align: center;
        display: flex;
        background-color: rgba(255, 255, 255, 0.2);
        font-weight: bold;
    }

    .heroQueueTwoVSVS {
        justify-content: center;
        align-items: center;
        border-radius: 200%;
        text-align: center;
        display: flex;
        background-color: rgba(0, 0, 0, 0.2);
        font-weight: bold;
    }

    .heroPackSelected {
        justify-content: center;
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1em;
        max-width: min-content;
        margin: 0 0 0 0;
        border-radius: 10px;

    }

    .heroPackSelected div {
        margin: 2px;
        word-break: break-word;
        font-size: 0.8em;
    }

    .heroHeader {
        display: flex;
        max-height: min-content;
    }

    .heroHeader > a {
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 1em;
        font-weight: 700;
        text-decoration: none;
    }

    .heroHeader > a > img {
        max-width: 9.96em;
        max-height: 5.6em;
        vertical-align: center;
    }

    button {
        text-transform: none !important;
    }

    .heroCounterHeroGrid {
        margin: 0 0 0 0;
        display: flex;
        border-radius: 10px;
        flex-wrap: wrap;
    }

    .heroADBox {
        display: flex;
        flex-direction: column;
        flex-wrap: nowrap;
        align-content: center;
        justify-content: center;
        align-items: center;
    }

    .heroEditPackButton {
        /* width: 100%*/
    }

    .heroGrid {

    }

    .closeButton {
        min-width: min-content !important;
        padding: 4px 4px 0 1px !important;
        align-items: flex-start !important;
    }

    .heroIcon {
        border-radius: 10px;
        overflow: hidden;
        border: 1px solid white;
        max-height: 5em;
        max-width: 5em;
        width: 5em;
        height: 5em;
        min-height: 5em;
        min-width: 5em;
        /*height: 80px;
        width: 80px;*/
    }

    .closeIcon {
        max-height: 1em;
        max-width: 1em;
        width: 1em;
        height: 1em;
        min-height: 1em;
        min-width: 1em;
        margin-left: 0.2em;
        /*height: 80px;
        width: 80px;*/
    }

    .heroIconSpan {
        width: 100%;
        text-align: center;
        word-break: break-word;
    }

    .heroPackSelectedExtStatus {
        grid-column: 1 / 6;
        grid-row: 2 / 2;
        /*justify-self: center;*/
    }


    @keyframes App-logo-spin {
        from {
            transform: rotate(0deg);
        }
        to {
            transform: rotate(360deg);
        }
    }

    .heroProb {
        /*position: absolute;*/
        padding: 0 0 1em 0;
        margin: -4em 1em 0 0.5em;
        text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
        user-select: none;
        z-index: 10;
    }

    .heroIconGrid {
        flex-basis: 0 !important;
        max-width: max-content !important;
    }

    .heroCounterGrid {

    }

    @media screen and (min-width: 500px) {
        .heroQueueTwoBox {

        }
    }
    .heroQueueTwoBox {
        display: flex;
        flex-direction: column;
        margin-bottom: 3px;
        border-radius: 10px;
        max-width: min-content;
    }

    .heroBoxQueueWithNum {
        max-width: min-content;
    }
}

@media screen and (min-width: 800px) {
    .heroQueueTwoBox > div {
        display: flex;
        flex-direction: row;
    }
    .heroIconGridEmpty {
        max-width: 5em;
        min-width: 5em;
        min-height: 7.3em;
    }

    .heroQueueTwoVSBox {
        grid-template-rows: 1fr 1fr 1fr !important;
        grid-template-columns: 1fr !important;
    }

    .heroQueueTwoVSPosition {
        justify-content: center;
        align-items: center;
        border-radius: 200%;
        text-align: center;
        display: flex;
        background-color: rgba(255, 255, 255, 0.2);
        font-weight: bold;
    }

    .heroIcon {
        max-height: 6em !important;
        max-width: 6em !important;
        width: 6em !important;
        height: 6em !important;
        min-height: 6em !important;
        min-width: 6em !important;
        user-select: none;
        /*height: 80px;
        width: 80px;*/
    }
}

